.main-navigation {
	display: block;
        
	font-family: $font__sans;
	font-weight: 400;
	clear: left;
        
        @include mobile {
            position: absolute;
            top: 1em;
            right: 0;
            z-index: 2525;
            
        }

	ul {
                background-color: rgba($color__skin-acc1, .9);
		display: none;
		list-style: none;
		margin: 0;
		padding-top: 1em;
		padding-left: 0;

		ul {
                        position: relative;
			display: none;
			z-index: 99999;
                        
                        &.toggled-on:not(.disabled) {
                            display: block;
                        }

			ul {
				top: 0;
			}

			li {
                                
				padding-left: 1em;

				&:hover > ul,
				&.focus > ul {
                                    //left: 100%;
				}
			}

			a {
                                
				width: 200px;
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;
	}

	a {
                
		display: inline-block;
		width: 100%;
		padding: .5em 1em .5em 0;
		text-decoration: none;
		
	}
        
        a {
            color: $color__skin-main;
        }

	a:hover,
	a:focus {
		text-decoration: underline;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}

	.menu-item-has-children,
	.page_item_has_children {
		min-width: 218px;
	}
	.menu-item-has-children > a,
	.page_item_has_children > a {
		padding-right: 2em;
	}
        
    div.menu-tray {
        display: block;
        ul {
            background-color: $color__skin-acc2;
            li a {
                color: $color__skin-main;
            }
        }
    }
}

button.dropdown-toggle {
	position: absolute;
        z-index: 9999;
	right: 0;
	border: none;
	background: inherit;
        color: $color__skin-main;
	line-height: 1.5em;
	padding: .4em 1em .4em .5em;
        
        @include tabletPlus {
            color: $color__skin-acc1;
        }
}

.menu-toggle {
        position: relative;
        float: right;
        margin-right: 1rem;
	//top: 0;
	//right: 0;
	display: flex;
	//margin: 1.2em 1.2em 1.2em 0;
	box-sizing: border-box;
        padding: .6rem .8rem;
	font-size: 1.8em;
	
        font-family: $font__sans;
	color: $color__skin-dark;
	border: 1px solid hsla(0, 0%, 100%, .3);
        
        @include small {
            font-size: 1.2em;
        }
        
        &::before {
            content: "";
            display: block;
            width: 1em;
            height: 1em;
            margin-right: .5em;
            background: url('img/menu-icon.svg');
            background-size: contain;
        }
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on:not(.disabled) {
	display: block;
}

@include tabletPlus {
	.menu-toggle {
		display: none;
	}

	.main-navigation {
                
            background-color: rgba($color__skin-acc1, .9);    
            .menu-item-has-children > a,
            .page_item_has_children > a {
                    padding-right: 2em;
                    background: hsla(0, 0%, 100%, .1);
            }

            ul {
                    background-color: $color__skin-dark;
                    display: block;
                    display: flex;
                
                    flex-wrap: wrap;

                    ul {
                            flex-direction: column;
                            background: hsla(0, 0%, 100%, .1);
                            margin-left: 0;

                            li {
                                    padding-left: 0;

                                    a {
                                            width: 218px;
                                            background: none;
                                    }
                            }
                    }

                    li {

                            a {
                                    padding: .4em 1em;
                            }
                    }
            }
            
            a {
                color: $color__skin-acc1;
            }

	}

}

@include desktopPlus {
	.main-navigation {

		ul {
			justify-content: flex-end;
			padding-top: 0;

			/* If you want dropdowns to open on hover, uncomment this: */
			/*
			li:hover > ul,
				li:focus > ul {
				display: block;
			}
			*/
		}

	}
}